.tarea-formulario {
  display:flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content:center;
}

.tarea-input {
  width: 350px;
  font-size: 18px;
  background-color: white;
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0px 0px 4px;
  border: 2px solid #00471b;
  outline: none;
}

.tarea-boton {
  padding: 16px;
  font-size: 18px;
  font-family: Roboto, sans-serif;
  border: none;
  border-radius: 0px 4px 4px 0px;
  cursor: pointer;
  outline: none;
  background: #00471b;
  color: #fff;
  text-transform: capitalize;
}